import React from "react"
import { DomsStyled } from "./Doms.styled"
import DomsLists from "./DomsLists"
import DomsOneList from "./DomsOneList"

class Index extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
    }
  }
  render() {
    /*
     *
     * User "container" state/props/methods!
     * This "child" component is to simplify container's render() logic.
     *
     */
    let that = this.props.that
    let { domains_generic = [], domains_suggested = {}, show_expand_suggestions } = that.props
    let suggestions_keys = ["exact", "tld", "com", "word hack", "phrase hack", "name"]
    let suggestions_lists = domains_suggested
    if (!suggestions_lists["exact"]) {
      suggestions_lists["exact"] = domains_generic
    }

    /*
     *
     * Output lists
     *
     */
    // let render_list_keys = []
    // let render_lists = {}
    // /*
    //  * Mix in premium, by price
    //  */
    // // let aftermarket = new Set()

    /*
     *
     * View
     *
     */
    return (
      <DomsStyled className={"DomsStyled"}>
        {!!show_expand_suggestions
          ?
          <DomsLists
            that={that}
            suggestions_lists={suggestions_lists}
            suggestions_keys={suggestions_keys}
          />
          :
          <DomsOneList
            that={that}
            suggestions_lists={suggestions_lists}
            suggestions_keys={suggestions_keys}
          />
        }
      </DomsStyled>
    )
  }
}

export default Index
